	data() {
		return {
			basicsList: [{
				icon: 'usefullfill',
				name: '开始'
			}, {
				icon: 'radioboxfill',
				name: '等待'
			}, {
				icon: 'roundclosefill',
				name: '错误'
			}, {
				icon: 'roundcheckfill',
				name: '完成'
			}, ],
			basics: 0,
			numList: [{
				name: '开始'
			}, {
				name: '等待'
			}, {
				name: '错误'
			}, {
				name: '完成'
			}, ],
			num: 0,
			scroll: 0
		};
	},
	methods: {
		basicsSteps() {
			this.basics == this.basicsList.length - 1 ? this.basics = 0 : this.basics += 1;
		},
		numSteps() {
			this.num = this.num == this.length - 1 ? this.num = 0 : this.num + 1;
		},
		scrollSteps() {
			this.scroll = this.scroll == 9 ? this.scroll = 0 : this.scroll + 1;
		}
	}
}
